.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.tipografy {
  font-family: 'Roboto Condensed', sans-serif;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.spinner {
  top: 50%;
	left: 50%;
	transform: translate(50%, 50%);
}

.percentContainer {
  margin-top: 20px;
  text-align: center;
}